import axios from 'axios'

export const getAllMembers = () => axios.get('https://xbox-one-group.firebaseio.com/.json?auth=qT9V0Od73vHhLcBJj00s4A3tzYKJXGRzmJbVqJ3r')
export const postNewMember = (newMemeber) => axios.post('https://xbox-one-group.firebaseio.com/.json?auth=qT9V0Od73vHhLcBJj00s4A3tzYKJXGRzmJbVqJ3r', newMemeber)

export default {
  getAllMembers,
  postNewMember
}


