import React, { Component } from 'react'
import iconxone from '../imgs/xbox-buttons-set-1.svg'
import '../css/card.css'

class Card extends Component {
    render() {
        return (
            <div className="col">

                <div className="card text-white bg-dark mb-3" style={{ width: 300 + 'px' }}>
                    <div className="card-header">
                        <div className='row card-row'>
                            <div className="col-4">
                                <img src={iconxone} className="img-fluid" width='70%' />
                            </div>
                            <div className="col-">
                                <h5 className="card-title">{this.props.gamerTag}</h5>
                            </div>
                        </div>
                        <div className='row card-row'>
                            <div className="col-4"></div>
                            <div className="col-">
                                <h6 className="card-subtitle mb-2 text-muted ">{this.props.name}</h6>
                            </div>
                        </div>
                    </div>
                    <div className="card-body">
                        <div className="card-body">
                            <p className="card-text">{this.props.description}</p>
                        </div>
                        <a href="" className="card-link">Link do Perfil</a>
                    </div>
                </div>
            </div>
        )
    }
}
export default Card


