import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'
import logo from './imgs/icon.svg';

import * as firebase from 'firebase'

import './App.css';
import Cards from './Cards/Cards'
import Cadastro from './Cadastro/Cadastro'

var config = {
  apiKey: "AIzaSyBPzyEP4qXUCpxAM6h9PwLslLOtUtFBDRc",
  authDomain: "xbox-one-group.firebaseapp.com",
  databaseURL: "https://xbox-one-group.firebaseio.com",
  storageBucket: "bucket.appspot.com"
};
firebase.initializeApp(config);

class App extends Component {

  render() {
    return (
      <Router>
        <div className="App">

          <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">

            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>

            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav mr-auto">
                <li className="nav-item active col-md-5">
                  < Link to="/">Home</Link>
                </li>
                <li className="nav-item col-md-5">
                  < Link to="/new">Registrar-se</Link>
                </li>
              </ul>
              <form className="form-inline my-2 my-lg-0 col-md-4">
                <input className="form-control  mr-md-1" type="search" placeholder="Search" aria-label="Search" />
                <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
              </form>

              <a className="navbar-brand " data-toggle="modal" data-target="#exampleModalCenter" >
                <img src={logo} className="img-fluid " width='10%' alt="" />
              </a>

            </div>
          </nav>

          <div className="modal fade" id="exampleModalCenter" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLongTitle">Login</h5>
                  <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">

                  <form>
                    <div className="form-group">
                      <div className="input-group mb-3">
                        <input type="text" ref='gamerTag' placeholder='nome de usuario da Xbox Live...' className="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" />
                      </div>

                      <div className="input-group mb-3">
                        <input type="text" ref='name' placeholder='Senha' className="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" />
                      </div>
                    </div>
                    <button type="button" onClick={this.saveMember} className="btn btn-primary btn-lg btn-block" >Registrar-se</button>
                  </form>


                </div>
                <div className="modal-footer">
                  <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                </div>
              </div>
            </div>
          </div>


          <div className="container pt-5 pb-5">
            <Route exact path='/' component={Cards} />
            <Route exact path='/new' component={Cadastro} />
          </div>

          <div className="fixed-bottom mt-5">.
          <nav class="navbar navbar-dark bg-dark">
              <span className="navbar-brand mb-0 h1">Navbar</span>
            </nav>

          </div>

        </div>
      </Router>
    );
  }
}

export default App;



