import React, { Component } from 'react'
import API from '../API/API';
//import FirebaseAPI from '../API/FirebaseAPI'

class Cadastro extends Component {
    constructor(props) {
        super(props)
        this.state = {
            gamerTag: '',
            name: '',
            phone: '',
            games: ['']
        }
        this.saveMember = this.saveMember.bind(this)
    }

    saveMember(){
        const newMember = {
            gamerTag: this.refs.gamerTag.value,
            name: this.refs.name.value,
            phone: this.refs.phone.value,
            description: this.refs.description.value,
            games: ['']
        }
/*
        FirebaseAPI.writeNewMember( this.refs.gamerTag.value, 
            this.refs.name.value,
            this.refs.phone.value,
            this.refs.description.value,
            ['Trove'] )
*/
        API.postNewMember(newMember)
            .then(res=>console.log('res'))
    }

    render() {
        return (
            <div className='Cadastro mt-5'>
                <div className="card mb-3">
                    <div className="card-body">
                        <h5 className="card-title">Faa seu cadastro</h5>
                        <p className="card-text">Com intuito de ornaginzar e facilitar o encontro dos players de xbox one</p>
                        <form>
                            <div className="form-group">
                                <div className="input-group mb-3">
                                    <input type="text" ref='gamerTag' placeholder='nome de usuario da Xbox Live...' className="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" />
                                </div>

                                <div className="input-group mb-3">
                                    <input type="text" ref='name' placeholder='Nome completo...' className="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" />
                                </div>

                                <div className="input-group mb-3">
                                    <input type="text" ref='phone' placeholder='Telefone para contato...' className="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" />
                                </div>

                                <div className="input-group">
                                    <textarea className="form-control" ref='description' placeholder='Fale um pouco sobre voc(jogo preferido, genero preferido)' aria-label="With textarea"></textarea>
                                </div>
                            </div>
                            <button type="button" onClick={this.saveMember} className="btn btn-secondary btn-lg btn-block" >Registrar-se</button>
                        </form>
                    </div>
                </div>
            </div>
        )
    }

}
export default Cadastro



