import React, { Component } from 'react'
import Card from './Card';

import API from '../API/API'

class Cards extends Component {

    constructor(props) {
        super(props)

        this.state = { 
            membros: [] 
        }
        this.renderCards = this.renderCards.bind(this)
    }

    componentDidMount() {
        API.getAllMembers()
            .then((response) => {
                this.setState({ membros: response.data })
            })
    }

    renderCards(members){
        return(
            <Card key={members.gamerTag} gamerTag={members.gamerTag} name={members.name} description={members.description}/> 
        )
        
    }

    render() {
        return (
            <div className="row mt-5">
                {
                    Object.values( this.state.membros).map(this.renderCards)
                }
            </div>
        )
    }
}

export default Cards


